import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filtro'
})
export class FiltroPipe implements PipeTransform {

  transform(value: string): string {
    let newStr: string = "";
    for (var i = 0; i <= value.length-9; i++) {
      newStr += value.charAt(i);
    }
    return newStr;
}

}
